this.getToken()
this.getRoomId()
},
getToken() {
this.token = Util.pageUrlGetValue("token")
Util.setCookie("token", this.token, "aylives.cn")
},
getRoomId() {
this.currentRoomId = Util.pageUrlGetValue("currentRoomId")
Util.setCookie("currentRoomId", this.currentRoomId, "aylives.cn")
